.wp-block-create-block-wp-oer-resource-block {
	background-color: #21759b;
	color: #fff;
	padding: 2px;
}
.wp-block-wp-oer-resource-block { padding:25px; }
.wp-block-wp-oer-resource-block h3 { margin-bottom:15px; }
.wp-block-wp-oer-resource-block div { margin: 15px 0; }
.wp-block-wp-oer-resource-block .oer-resource-block-subjects ul { margin:0; padding:0; }
.wp-block-wp-oer-resource-block .oer-resource-block-subjects ul li { list-style:none; display:inline-block; }
.wp-block-wp-oer-resource-block .oer-resource-block-subjects ul li { float:none; font-size: 14px; padding: 10px; }
.wp-block-wp-oer-resource-block .oer-resource-block-grade-levels { font-size:14px; }
.wp-block-wp-oer-resource-block .oer-resource-block-featured-image img { width:100%; height:auto; }